@media (max-width: 576px) {
  .my-component {
    width: 100%;
  }
}

@media (min-width: 576px) and (max-width: 768px) {
  .my-component {
    width: 50%;
  }
}

@media (min-width: 768px) {
  .my-component {
    width: 33.33%;
  }
}

.container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content {
  flex: 1;
}
